.coin-details {
  &__topbar {
    display: flex;
    align-items: center;
    margin: {
      left: var(--coin-details__topbar_margin-left);
      top: var(--coin-details__topbar_margin-top);
      bottom: var(--coin-details__topbar_margin-bottom);
    }
  }

  &__back {
    margin-right: var(--coin-details__back_margin-right);
  }

  &__pic {
    margin-right: var(--coin-details__pic_margin-right);
  }

  &__sig {
    display: flex;
    align-items: baseline;
  }

  &__name {
    margin-right: var(--coin-details__name_margin-right);
  }

  &__price-details {
    display: flex;
    align-items: baseline;
    margin: {
      left: var(--coin-details__price-details_left);
      top: var(--coin-details__price-details_top);
    }
  }

  &__current-price {
    margin-right: var(--coin-details__current-price_margin-right);
    font: {
      size: var(--coin-details__current-price_font_size);
      weight: 500;
    }
  }
}