@use "@styles/pseudo-elements" as *;

.search-by-name-input {
  position: relative;
  &__input {
    border: {
      width: 1px;
      style: solid;
      radius: var(--search_border-radius);
      color: var(--search_border-color);
    }
    padding: {
      top: var(--search-name__padding-top);
      bottom: var(--search-name__padding-bottom);
      left: var(--search-name__padding-left);
    }
    &::placeholder {
      color: var(--search-name_color);
    }
  }
  &::after {
    @include search-icon(var(--search-name__icon-size));
    margin: {
      left: 10px;
    }
  }
}