@use "@styles/sizes" as *;
@use "@styles/pseudo-elements" as *;

.button {
  cursor: pointer;
  &_linked {
    border: none;
    background: inherit;
  }
  &_with-icon {
    &-search {
      position: relative;
      @include square(var(--button__icon-search_size));
      &::before {
        @include search-icon(var(--button__icon-search_size));
      }
    }
  }
}