@mixin square($size) {
  width: $size;
  height: $size;
}

.card {
  width: var(--card_width);
  height: var(--card_height);
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 var(--coin-item_padding);
  margin: {
    left: var(--coin-item_margin-left);
    bottom: var(--coin-item_margin-bottom);
  }
  &_theme_light {
    background: {
      color: var(--coin-item_theme_light_bg-color);
    }
    box-shadow: var(--coin-item_theme_light_shadow);
  }
}