@mixin arrow-square($size, $width: 2px, $color: black, $angle: 45deg) {
    @include pseudo-element();
    display: block;
    @include square($size);
    border-style: solid;
    border {
        color: $color;
    }    
    border: {
        top: 0;
        right: 0;
    }
    @include place-icon-in-center($size);
    transform: rotate( $angle );
}

@mixin search-icon($size) {
    @include pseudo-element();
    display: inline-flex;
    background: {
        image: url("../../assets/iconsearch.svg");
        size: cover;
    }
    @include square($size);
    @include place-icon-in-center($size);
}

@mixin pseudo-element() {
    content: "";
    position: absolute;
}

@mixin square($size) {
    width: $size;
    height: $size;
}

@mixin place-icon-in-center($size) {
    left: 0px;
    position: absolute;
    margin-top: calc($size / 2 * -1);
    top: 50%;
}