@use '@styles/sizes' as *;
@use '@styles/colors' as *;

.coin-item {
  width: var(--coin-item_width);
  height: var(--coin-item_height);
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 var(--coin-item_padding);
  cursor: pointer;

  margin: {
    left: var(--coin-item_margin-left);
    bottom: var(--coin-item_margin-bottom);
  }

  &_theme_light {
    background: {
      color: var(--coin-item_theme_light_bg-color);
    }
    box-shadow: var(--coin-item_theme_light_shadow);
  }

  &__left-col {
    display: flex;
  }

  &__pic {
    margin: {
      right: var(--coin-item__pic_margin-right);
    }
  }

  &__sig {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
  }

  &__symbol {
    font: {
      size: var(--coin-item__symbol-size);
    }
    &_theme_light {
      color: var(--coin-item__symbol_theme_light_color);
    }
  }

  &__right-col {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
  }

  &__current-price {
    font: {
      style: normal;
      weight: var(--coin-item__current-price_font-weight);
      size: var(--coin-item__current-price_font-size);
    }
    line-height: var(--coin-item__current-price_line-height);
    &_theme_light {
      color: var(--coin-item__current-price_theme_light_color);
    }
  }

  &__price-change {
    font: {
      style: normal;
      weight: var(--coin-item__price-change_font-weight);
      size: var(--coin-item__price-change_font-size);
    }
    line-height: var(--coin-item__price-change_line-height);
  }
}