@use "@styles/sizes" as *;
@use "@styles/colors" as *;
@use "@styles/elements" as *;

@mixin loaderSize($size) {
  @include square(var(--loader_size_#{$size}));
  border: {
    width: var(--loader_border-width_#{$size});
  }
}

.loader {
  display: flex;
  justify-content: center;
  padding: {
    top: var(--loader_padding);
    bottom: var(--loader_padding);
  }
  &__circle {
    border: {
      radius: 50%;
      style: solid;
      color: var(--loader_border-color_fill) var(--loader_border-color_transparent) var(--loader_border-color_fill) var(--loader_border-color_fill);
    }
    &_size {
      &_s {
        @include loaderSize(s);
      }
      &_m {
        @include loaderSize(m);
      }
      &_l {
        @include loaderSize(l);
      }
    }
    animation: 2s infinite ease-in-out spinned;
  }
}

@keyframes spinned {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
