@use "@styles/sizes" as *;

@mixin select__input_bg($icon) {
  background: url("../../assets/#{$icon}.svg") no-repeat 95%;
  background: {
    color: var(--select_bg-color);
  };
}

.select {
  position: relative;
  width: var(--select-width);
  &__input {
    cursor: pointer;
    position: relative;
    color: var(--select_color);
    font: {
      size: var(--select_font-size);
    }
    padding: {
      top: var(--select__input_padding_top);
      bottom: var(--select__input_padding_bottom);
      left: var(--select__input_padding_left);
    }
    width: calc(var(--select-width) - var(--select__input_width-fix));
    border: {
      width: 1px;
      style: solid;
      color: var(--select_border-color);
    }
    &_close {
      @include select__input_bg(triangle);
      border: {
        radius: var(--select_border-radius);
      }
    }
    &_open {
      @include select__input_bg(arrowup);
      border: {
        bottom: none;
        top: {
          left: {
            radius: var(--select_border-radius);
          }
          right: {
            radius: var(--select_border-radius);
          }
        }
      }
    }
  }
  &__list {
    height: var(--select__input_width-height);
    overflow-y:auto;
    position: absolute;
    width: inherit;
    background: {
      color: var(--select_bg-color);
    };
    border: {
      width: 1px;
      style: solid;
      color: var(--select_border-color);
      top: none;
      bottom: {
        left: {
          radius: var(--select_border-radius);
        }
        right: {
          radius: var(--select_border-radius);
        }
      }
    }
    &_close {
      display: none;
    }
    &_open {
      display: block;
    }
  }
  &__option {
    cursor: pointer;
    color: var(--select_color);
    font: {
      size: var(--select_font-size);
    }
    padding: {
      top: var(--select__input_padding_top);
      bottom: var(--select__input_padding_bottom);
      left: var(--select__input_padding_left);
    }
    border: {
      top: {
        width: 1px;
        style: solid;
        color: var(--select_border-color);
      }
    }
  }
}